export default {
	data() {
		return {
			farmProduceList:{},
			treeNumInfo:{
				tree_num:0,
				used_tea_num:0,
				can_use_tea_num:0
			},
		};
	},
	onLoad() {
		this.isLogin();
		this.farmProduceInfo();
	},
	onShow() {
		this.getTreeAllInfo();
	},
	methods: {
		shareInfo(){
			uni.share({
				provider:"weixin",
				type:0,
				scene:"WXSceneSession",
				title:"QQ分享",
				href:"www.baidu.com",
				imageUrl:"http://chayuan.vswxx.top/public/uploads/attach/2020/03/12/5e698c08b5768.png",
				success: (res) => {
					console.log(res);
				},
				fail: (res) => {
					console.log("err",res);
				}
			})
		},
		getTreeAllInfo() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'user_tree_all_info'
				}),
				function(res) {
					console.log(res);
					that.treeNumInfo=res.data;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToMap() {
			let that=this;
			// uni.navigateTo({
			// 	url:"/pages/map/map?mapInfo="+JSON.stringify(that.farmProduceList.merInfo[0].list[0])
			// })
			uni.getLocation({
			 type: 'gcj02', //返回可以用于uni.openLocation的经纬度 
				success: function (res) {
					const latitude = res.latitude;
					const longitude = res.longitude;
					uni.openLocation({
						latitude: latitude,
						longitude: longitude,
						success: function () {
							
						},
					});
				}
			})
		},
		farmProduceInfo() {
			let that = this;
			that.goodsList = [];
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_merchant_index'
				}),
				function(res) {
					that.farmProduceList = res.data;
					console.log(res);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToMoreTreeList(){
			uni.navigateTo({
				url:'/pages/tabber/home/more_tree_list/more_tree_list'
			})
		},
		navToExchangeList(){
			uni.navigateTo({
				url:'/pages/subscribe/exchange_list/exchange_list'
			})
		},
		// navToTransplantTree(){
		// 	uni.navigateTo({
		// 		url:'/pages/subscribe/transplant_tree/transplant_tree'
		// 	})
		// },
		navToManorDetails(){
			uni.navigateTo({
				url:'/pages/subscribe/manor_details/manor_details'
			})
		}
	}
};
